







import {mapActions, mapState} from 'vuex';
import {KeyValue} from "../entities/Key";

export default {
  name: "ResetFile",
  computed: {
    ...mapState(['issues']),
    isVisible(): boolean {
      return this.issues.length > 0;
    }
  },
  methods: {
    ...mapActions([
      'resetFile',
    ])
  }
}
