














import {mapActions,mapState} from 'vuex';

type FileEventTarget = EventTarget & { files: FileList };
type FileInputEvent = Event & { target: FileEventTarget };

export default {
  name: "FileLoader",
  computed: {
    ...mapState(['file']),
    isVisible(): boolean {
      return this.file === null;
    }
  },
  methods: {
    ...mapActions([
      'setFile',
    ]),
  }
}
